export default {
  data() {
    return {
      list: [],
      //   
      pagenum: 1,
      //   
      pagesize: 10,
      //   
      count: 0,
      searchValue: "",
      //   
      currentRoleId: -1,
      // 
      currentName: "",
      // id scope.row
      currentUserId: -1,
      //   
      addUserdialogFormVisible: false,
      // 
      editUserdialogFormVisible: false,
      //
      setRoledialogVisible: false,
      // 
      roles: [],
      // 
      form: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 12, message: ' 6  12 ', trigger: 'blur' }
        ]
      }
    };
  },
  created: function () {
    this.loadList();
  },
  methods: {
    // 
    async loadList() {
      var res = await this.$http.get(
        `/users?pagenum=${this.pagenum}&pagesize=${this.pagesize}&query=${this.searchValue
        }`
      );
      let {
        meta: { status, msg }
      } = res.data;
      // 
      if (status == 200) {
        // 
        let {
          data: {
            data: { pagenum, total }
          }
        } = res;
        // 
        this.pagenum = pagenum;
        // 
        this.count = total;
        let {
          data: {
            data: { users }
          }
        } = res;
        this.list = users;
      } else {
        this.$message.warning(msg);
      }
    },
    // 
    async jurisdiction() {
      let res = await this.$http.put(`users/${this.currentUserId}/role`, {
        rid: this.currentRoleId
      })
      let { data: { meta: { msg, status } } } = res;
      if (status == 200) {
        this.setRoledialogVisible = false
        this.$message.success(msg);
      } else {
        this.$message.error(msg);
      }
    },
    //  
    // listlist
    async deleteHandle(id) {
      let res = await this.$http.delete(`/users/${id}`)
      let { data: { meta: { status, msg } } } = res;
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        if (status == 200) {
          //  1 
          if (this.list.length = 1 && this.pagenum !== 1) {
            this.pagenum--;
            this.loadList();
          }
          this.$message({
            type: 'success',
            message: '!'
          });
          this.loadList()
        }
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    // 
    async userStatus(user) {
      let res = await this.$http.put(`users/${user.id}/state/${user.mg_state}`)
      let { data: { meta: { msg, status } } } = res;
      if (status == 200) {
        this.$message.success(msg)
      } else {
        this.$mesasage.error(msg);
      }
    },
    // 
    async checktOutHandle(user) {
      this.setRoledialogVisible = true
      this.currentName = user.username;
      this.currentUserId = user.id;
      let res = await this.$http.get(`/roles`);
      let { data: { meta: { msg, status } } } = res;
      this.roles = res.data.data;
      //  id
      let userResdata = await this.$http.get(`/users/${user.id}`);
      this.currentRoleId = userResdata.data.data.rid;
    },
    //
    editHandleClik(user) {
      // 
      this.editUserdialogFormVisible = true
      // 
      this.form.username = user.username;
      this.form.email = user.email;
      this.form.mobile = user.mobile;
      this.form.id = user.id;
    },
    // 
    async editList() {
      // 
      let res = await this.$http.put(`users/${this.form.id}`, {
        email: this.form.email,
        mobile: this.form.mobile
      });
      //  
      let { data: { meta: { msg, status } } } = res;
      if (status === 200) {
        this.editUserdialogFormVisible = false;
        this.$message.success(msg)
        this.from.username = '';
        this.form.email = '';
        this.form.mobile = '';
      } else {
        this.$message.error(msg)
      }
    },
    // 
    editHandle() {
      this.form.username = '';
      this.form.email = '';
      this.form.mobile = '';
      this.editUserdialogFormVisible = false;
    },
    //  
    handleSizeChange(val) {
      //  
      this.pagesize = val;
      this.loadList();
    },
    // 
    handleCurrentChange(val) {
      //   
      this.pagenum = val;
      //
      this.loadList();
    },
    // 
    handleSearch() {
      this.loadList();
    },
    // 
    async addList() {
      this.$refs.addForm.validate(async (valid) => {
        if (valid) {
          let res = await this.$http.post('/users', this.form);
          const { data: { meta: { status, msg } } } = res;
          if (status == 201) {
            // 
            // this.form = ''
            // 
            this.$message.success('');
            // 
            this.addUserdialogFormVisible = false;
            // 
            this.loadList();
            this.$refs.addForm.resetFields();
          } else {
            // 
            this.$message.error(msg)
          }
        } else {
          return false;
        }
      });
    },
    // 
    errorHandle() {
      // 
      this.addUserdialogFormVisible = false;
      // 
      this.$refs.addForm.resetFields();
    },
  }
};


