<template>
    <el-card class="card">
      <my-breadcrumb level1="" level2=""></my-breadcrumb>
      <el-alert
        title=""
        class="alert"
        :closable="false"
        type="info"
        center
        show-icon>
      </el-alert>
      <!--  -->
      <el-steps :active="active" finish-status="success" align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>
      <!--  -->
      <el-form
      labelPosition="top"
      :model="ruleForm"
      label-width="100px">

        <el-tabs @tab-click="handleClick" 
        :tab-position="tabPosition"
        style="height: 100%;margin-top:20px;">
            <el-tab-pane  label="">
               <el-form-item label="">
                <el-input v-model="form.goods_name"></el-input>
               </el-form-item>
               <el-form-item label="">
                <el-input v-model="form.goods_price"></el-input>
               </el-form-item>
               <el-form-item label="">
                <el-input v-model="form.weight"></el-input>
               </el-form-item>
               <el-form-item label="">
                <el-input v-model="form.goods_number"></el-input>
               </el-form-item>
               <el-form-item label="">
                   <el-cascader
                        placeholder=""
                        clearable
                        :props="{label:'cat_name', value:'cat_id',children:'children'}"
                        expand-trigger="hover"
                        :options="options"
                        v-model="selectedOptions"
                        @change="handleChange">
                    </el-cascader>
               </el-form-item>
            </el-tab-pane>
            <el-tab-pane  label="">
                <el-form-item
                v-for="item in dynamicParams"
                :key="item.attr_id"
                :label="item.attr_name">
                      <el-checkbox
                      checked
                      border
                      v-for="param in item.params"
                      :label="param"
                      :key="param">
                      </el-checkbox>
                </el-form-item>  
            </el-tab-pane>
            <el-tab-pane label="">
                <el-form-item
                v-for="item in staticParams"
                :key="item.attr_id"
                :label="item.attr_name">
                <el-input v-model="item.attr_vals"></el-input>
                </el-form-item>
            </el-tab-pane>
            <el-tab-pane label="">
                <!-- action file-list-->
                <el-upload
                    :headers="headers"
                    class="upload-demo"
                    action = "http://localhost:8888/api/private/v1/upload"
                    :on-remove="handleRemove"
                    :on-success="handleSuccess"
                    list-type="picture">
                    <el-button size="small" type="primary"></el-button>
                </el-upload>
            </el-tab-pane>
            <el-tab-pane label="">
            <el-button @click="handleAdd"></el-button>
                <!--  -->
                <quill-editor v-model="form.goods_introduce">

                </quill-editor>
            </el-tab-pane>
        </el-tabs>
        </el-form>
        <!--  -->
    </el-card>
</template>
<script>
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'
export default {
     components:{
            quillEditor
        },
      data(){
        return{
        //active0 1 
         active:0,
        //  
        ruleForm:{

        },
         tabPosition:'left',
        //  
        dynamicParams:[],
        // 
        staticParams:[],
        headers:{
             'Authorization': sessionStorage.getItem('token')
        },
         index:'',
         form:{
            //  
           goods_name:'',
           goods_price:'',
           goods_number:'',
           goods_weight:'',
           goods_introduce:'',
           attrs:[],
        //    id 1,2,3
           goods_cat:'',
           pics:[]
         },
        //    id
         options:[],
         selectedOptions:[]
        }
    },
    created(){
       this.loadOptions();
    },
    methods:{
        // 
        handleRemove(file,fileList){
            let  list = this.form.pics;
            for(var i = 0;i<list.length;i++){
                if(list[i] == file.response.data.tem_path){
                     this.index = i;
                }
            }
            this.form.pics.splice(this.index,1)
            console.log(this.form.pics)
        },
        // 
        async handleAdd(){
             this.form.goods_cat = this.selectedOptions.join(',');
            //  
             let arr1 = this.dynamicParams.map((item)=>{
                 return {'attr_id':item.attr_id,'attr_value':item.params.join(',')};
             });
            //  
            let arr2 = this.staticParams.map((item)=>{
                return { 'attr_id':item.attr_id,'attr_value':item.attr_vals}
            })
            // ,
            this.form.attrs = [...arr1, ...arr2];
            const res = await this.$http.post('goods',this.form);
            let {meta:{status,msg}} = res.data;
            if(status==201){
            //   this.$message.success(msg);
            this.$router.push('goods');
            }else{
                this.$message.error(msg)
            }
        },
        handleSuccess(response,file,fileList){
            if(response.meta.status==200){
               this.form.pics.push(response.tem_path) 
            }else{
                this.$message.error('')
            }
        },
        // 
        async loadParams(index){
            let sel = index ==='1' ? 'many':'only'
            let res = await this.$http.get(`categories/${this.selectedOptions[2]}/attributes?sel=${sel}`);
            if(sel === 'many'){
                this.dynamicParams = res.data.data;
                this.dynamicParams.map((item)=> {
                // 
                item.params = item.attr_vals.length== 0 ? [] : item.attr_vals.split(',');
            });
            }else{
                this.staticParams = res.data.data;
            }
           
        },
        handleClick(tab,event){
           this.active = tab.index - 0;
        //    tap 
        if(tab.index === '1' || tab.index === '2'){
            //
            if(this.selectedOptions.length !==3){
                this.$message.warning('');
            }else{
                this.loadParams(tab.index);
            }
            
        }
        },
        // 
        handleChange(){
           if(this.selectedOptions.length !== 3){
                //  
                this.selectedOptions.length = 0;
                this.$message.warning("");
           }
        },
        // 
        async loadOptions(){
            let res = await this.$http.get(`categories?type=3`);
            this.options = res.data.data;
        }
    }
}
</script>

<style>
.alert{
    margin-top: 10px;
    margin-bottom: 10px;
}
.el-step__title{
    font-size: 12px;
}
.el-checkbox{
    margin-bottom: 5px;
}
.ql-editor{
    height: 400px;
}
</style>



