<template>
   <el-card class="card">
       <!--   props -->
       <my-breadcrumb level1="" level2 = ""></my-breadcrumb>
        <el-row :span="24">
            <el-button type="success" class="roles" plain @click="handleAdd"></el-button>
        </el-row>
        <el-table
        border
        stripe
        v-loading="loading"
        :data="list"
      style="width: 100%">
    <el-table-column type="expand">
      <template slot-scope="scope">
       <!--  tableajax-->
       <el-row
       v-for="item in scope.row.children"
       :key="item.id">
           <el-col :span="4">
               <!--  -->
               <el-tag
               @close="deleteTag(scope.row,item.id)"
               type="success"
               closable
                 >{{item.authName}}</el-tag>
           </el-col>
           <el-col :span="20">
              <!--  -->
              <el-row
              v-for="level in item.children"
              :key="level.id">
                  <el-col
                  :span="4">
                      <el-tag
                      closable
                      type="warning"
                      @close="deleteTag(scope.row,level.id)"
                      >
                          {{level.authName}}
                      </el-tag>
                  </el-col>
                  <el-col
                  :span ="20"
                  >
                  <el-tag 
                  class="level2"
                  v-for ="level2 in level.children"
                  :key="level2.id"
                  type="danger"
                  closable
                  @close="deleteTag(scope.row,level2.id)"
                  >
                      {{level2.authName}}
                  </el-tag>
                  <el-col :span="20">
                  </el-col>
                </el-col>
              </el-row>
           </el-col>
       </el-row>
       <el-row v-if="scope.row.children==0">
          <el-col :span="24"></el-col> 
       </el-row>
      </template>
    </el-table-column>
    <el-table-column
       type="index"
        width="180">
      </el-table-column>
      <el-table-column
        prop="roleName"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="roleDesc"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        label="">
         <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" circle plain></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" plain circle></el-button>
          <el-button @click="dakaiTree(scope.row)" type="success" icon="el-icon-check"  size="mini" plain circle></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
        title=""
        :visible.sync="setRolesdialogVisible"
        width="30%"
    >
    <!--  -->
        <el-tree
        show-checkbox
        ref="tree"
        :default-expanded-keys="[2, 3]"
        :default-checked-keys="checkList"
        node-key="id"
        default-expand-all
        :data="treeList"
        :props="defaultProps"
        >
        </el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="setRolesdialogVisible = false"> </el-button>
            <el-button @click="handleSetright" type="primary"> </el-button>
        </span>
    </el-dialog>
    <!--  -->
    <el-dialog
     title=""
     :visible.sync = "addListStatus">
      <el-form
      label-width="100px"
      :rules="rules"
      ref = "addForm"
      :model = "form">
        <el-form-item label="" prop="roleName">
          <el-input v-model="form.roleName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input  v-model="form.roleDesc" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click = "handleAddList"> </el-button>
      </div>
    </el-dialog>
   </el-card>
</template>
<script>
export default {
    data(){
        return {
            list:[],
            loading:true,
            setRolesdialogVisible:false,
            // 
            roleId:-1,
            // 
            form:{
                roleName:'',
                roleDesc:''
            },
            rules: {
                roleName: [
                { required: true, message: '', trigger: 'blur' }
                ],
                roleDesc: [
                { required: true, message: '', trigger: 'blur' }
                ]
             },
            addListStatus:false,
            // 
            treeList:[],
            // 
            checkList:[],
            defaultProps:{
            label:'authName',
            children:'children'
            }
        }
    },
    created(){
     this.loadList();
    },
    methods:{
     async loadList(){
         let res = await this.$http.get('/roles');
         let { meta: { msg, status } } = res.data;
         this.loading = false;
         if(status == 200){
             this.list = res.data.data;
         }else{
             this.$message.error(msg); 
         }
     },
      handleAdd(){
        this.addListStatus =true;
        this.form.roleName= '';
        this.form.roleDesc = '';
    },
    //  
     async handleAddList(){
         // 
      this.$refs.addForm.validate(async (valid) => {
        if (!valid) {
          // 
          return;
        }
        // 
        this.addListStatus = false;
        const res = await this.$http.post('/roles', this.form);
        if (res.data.meta.status === 201) {
          this.$message({
            type: 'success',
            message: ''
          });
          this.form = '';
          // 
          this.loadList();
        } else {
          this.$message({
            type: 'erroe',
            message: ''
          });
        }
      });
     },
    //  
      cancel(){
        this.form.roleName = '';
        this.form.roleDesc = '';
        this.addListStatus = false
      },
    //  x
     async deleteTag(role,rightId){
         let res = await this.$http.delete(`/roles/${role.id}/rights/${rightId}`)
        //  
        let { data: { meta: { msg,status } } } = res;
        if(status == 200){
            this.$message.success(msg);
        //    
            role.children = res.data.data;
        }else{
            this.$message.error(msg);
        }
        
     },
    //   
    async handleSetright(){
    //  roleId id
    // rids id
    const checkListId = this.$refs.tree.getCheckedKeys();
    const halfCheckedListId = this.$refs.tree.getHalfCheckedKeys();
    // 
    let arr = [...checkListId,...halfCheckedListId];
    let res = await this.$http.post(`/roles/${this.roleId}/rights`,{
        rids:arr.join(',')
        
    })
    let { data: { meta: { msg, status} } } = res;
    // 
     if(status==200){
        this.$message(msg);
        // 
        this.setRolesdialogVisible = false;
        //  
        this.loadList()
    }else{
       this.$message.error(msg)
    }
    },
    
    //  ,role
     async dakaiTree(role){
        //  roleId,
         this.roleId = role.id;
         this.setRolesdialogVisible = true;
         let res = await this.$http.get('/rights/tree')
         this.treeList = res.data.data;
         //idcheckList,level2
         this.checkList = [];
           role.children.map((item)=>{
              item.children.map((level)=>{
                  level.children.map((level2)=>{
                      this.checkList.push(level2.id)
                  })
              })
           })
     },
     
    }
}
</script>
<style>
.roles{
    margin-top: 10px;
    margin-bottom: 10px;
}
.level2{
    margin-bottom: 10px;
    margin-left:10px;
}
</style>



