<template>
    <el-card>
        <!--  -->
        <my-breadcrumb level1="" level2=""></my-breadcrumb>
        <!--  -->
        <el-alert
        :closable="false"
        title=":"
        type="warning"
        show-icon>
        </el-alert>
        <el-row :span="24">
            :
            <!--  -->
            <el-cascader
            placeholder=""
            expand-trigger="hover"
            :options="options"
            :props=" { label:'cat_name', value:'cat_id' }"
            v-model="selectedOptions"
            @change="handleChange">
           </el-cascader>
           <!-- tab -->
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="" name="many">
                    <el-button type="primary" :disabled="this.selectedOptions.length !==3"></el-button>
                    <el-table
                    border
                    stripe
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                    type="expand">
                        <template slot-scope="scope">
                            <!-- tag -->
                            <el-tag
                            v-for="item in scope.row.params"
                            :key="item"
                            closable
                            @close="handleClose(item,scope.row)">
                            {{item}}
                            </el-tag>
                            <el-input
                            class="input-new-tag"
                            v-if="inputVisible"
                            v-model="inputValue"
                            ref="saveTagInput"
                            size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)"
                            @blur="handleInputConfirm(scope.row)"
                            >
                            </el-input>
                            <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column
                    type="index"
                    width="60">
                    </el-table-column>
                    <el-table-column
                    prop="attr_name"
                    label=""
                    width="180">
                    </el-table-column>
                    <el-table-column
                    label="">
                      <template slot-scope="scope">
                           <el-button type="success" icon="el-icon-edit" plain></el-button>
                           <el-button type="success" icon="el-icon-delete" plain></el-button>
                      </template>
                    </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="" name="only">
                    <el-button type="primary" :disabled ="this.selectedOptions.length !==3"></el-button>
                    <el-table
                    border
                    stripe
                    :data="tableData"
                    style="width: 100%">
                    <el-table-column
                    type="index"
                    width="60">
                    </el-table-column>
                    <el-table-column
                    prop="attr_name"
                    label=""
                    width="180">
                    </el-table-column> <el-table-column
                    prop="attr_vals"
                    label=""
                    width="180">
                    </el-table-column>
                    <el-table-column
                    label="">
                      <template slot-scope="scope">
                           <el-button type="success" icon="el-icon-edit" plain></el-button>
                           <el-button type="success" icon="el-icon-delete" plain></el-button>
                      </template>
                    </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </el-row>
    </el-card>
</template>
<script>
export default {
    data(){
        return{
            // 
          options:[],
        //   
          selectedOptions:[],
        //   name
          activeName: "many",
          tableData:[],
        //   
          inputVisible : false,
          inputValue:''
        };
    },
    created(){
        this.loadOptions();
    },
    methods:{
        // 
        // 
        handleChange(){
            this.loadData()
        },
      async loadOptions(){
         let res = await this.$http.get('categories?type=3')
           this.options = res.data.data;
      },
    //   
     async loadData(){
        //  
         if(this.selectedOptions.length === 3){
            //  
             this.tableData.length = 0;
            let res = await this.$http.get(`categories/${this.selectedOptions[2]}/attributes?sel=${this.activeName}`)
            const {meta:{msg,status}} = res.data;
            // 
            if(status==200){
                // 
                this.tableData = res.data.data;
                // attr_vals =
                if(this.activeName =="many"){
                    this.tableData.forEach((item)=>{
                        // vue
                           let arr = item.attr_vals.length == 0 ? [] : item.attr_vals.split(',')
                        //   
                           this.$set(item, 'params', arr)
                    })
                }
            }
            }else{
                this.$message.error(msg);
         } 
       
      },
      handleClick(){
          this.loadData();
      },
    //   
      async handleInputConfirm(row){
        //  
        let inputValue = this.inputValue;
        // return
        if (inputValue) {
          // 
        row.params.push(inputValue);
        // 
        let res = await this.$http.put(`categories/${this.selectedOptions[2]}/attributes/${row.attr_id}`,{
          attr_vals:row.params.join(','),
          attr_name:row.attr_name,
          attr_sel: this.activeName
        })
        let { meta : {msg,status}} =res.data;
        //   tag
        if(status == 200){
          this.$message.success(msg);
          this.inputVisible = false;
          this.inputValue = '';
        }else{
          this.$message.error(msg);
        }
        // tag
        }else{
          this.inputVisible = false;
          this.inputValue = '';
        }  
    },
    // tag
       showInput(){
        // 
        this.inputVisible = true;
        //  $nextTickdom setTimeOut
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus();
        });
    },
    //   
      async handleClose(tag,param){  
          // id this.selectionOptions
         // id params.attr_id
         //  js  
              //  item
                    // params tag
                let index = param.params.findIndex((item)=>{
                    if ( tag === item ){
                        return true;
                    }
                })
               param.params.splice(index,1);
          let res = await this.$http.put(`categories/${this.selectedOptions[2]}/attributes/${param.attr_id}`,{
              'attr_vals': param.params.join(','),
              'attr_sel': this.activeName,
              'attr_name': param.attr_name
          });
          let { meta:{msg,status}} = res.data;
          if(status == 200){
               this.$message.success(msg)

          }else{
              this.$message.error(msg);
          }

      }

    }
}
</script>
<style>
    .el-tag + .el-tag{
        margin-left: 10px;
    }
    .button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
  }
  .el-card{
    height: 100%;
  }
  .el-button{
    margin-bottom: 10px;
  }
  .input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
  }
</style>


