<template>
    <el-card class="card">
      <!--  -->
      <my-breadcrumb level1="" level2=""></my-breadcrumb>
       <el-table
      :data="tableData"
      stripe
      border
      style="width: 100%,height:100%,margin-top: 10px">
      <el-table-column
      type="index"
      width="60">
      </el-table-column>
      <el-table-column
        prop="order_number" 
        label=""
        width="250">
      </el-table-column>
      <el-table-column
        prop="order_price"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        label=""
        width="100">
        <template slot-scope="scope">
          <el-tag v-if = "scope.row.order_pay==0" type="danger"></el-tag>
          <el-tag v-else = "scope.row.order_pay==1" type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="is_send"
        label=""
        width="100">
      </el-table-column>
       <el-table-column
        label=""
        width="250">
        <template slot-scope="scope">
        {{scope.row.create_time|formDate('YYYY-MM-DD')}}
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
           <el-button
           type="primary"
           icon="el-icon-edit"
           size="mini"
           plain
           @click="showClick">
           </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[10, 20, 30, 40]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <!--  -->
    <el-dialog title=""
    :visible.sync="dialogFormVisible">
        <el-form
        :label-width="formLabelWidth"
        :model="form">
          <el-form-item label="/">
            <!--  -->
            <el-cascader
              expand-trigger="hover"
              :options="options"
              v-model="selectedOptions"
              @change="handleChange">
            </el-cascader>
          </el-form-item>
          <el-form-item label="">
              <el-input v-model="form.address" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
        </div>
      </el-dialog>
    </el-card>
</template>
<script>
import  citydata from './city_data2017_element.js';

export default {
    data(){
        return {
            tableData:[],
            // 
            // 
            pagenum:1,
            // 
            pagesize:10,
            // 
            total:0,
            // 
            dialogFormVisible:false,
            // 
            options:[],
            selectedOptions:[],
            form:{
              // 
              address: '',
              // 
              region:[]
            }
        }; 
    },
    created(){
      this.loadList();
    },
    methods:{ 
       async loadList(){
       let res = await this.$http.get(`orders?pagenum=${this.pagenum}&pagesize=${this.pagesize}`);
            this.total = res.data.data.total;
            this.tableData = res.data.data.goods;
     },
      handleSizeChange(val) {
        this.pagesize = val;
        this.loadList();
      },
      handleCurrentChange(val) {
        this.pagenum = val;
        this.loadList();
      },
      // 
     async showClick(){
      //  
       this.dialogFormVisible = true;
      //  
      this.options = citydata;
     }
    }

}  
</script>
<style>
 .el-table{
     margin-top: 10px;
     margin-bottom: 10px;
 }
</style>



