<template>
  <div class="login-warp">
    <el-form
    class="login-form"
    label-position="top"
    label-width="80px"
    :model="formData">
    <h2></h2>
  <el-form-item label="">
    <el-input v-model="formData.username"></el-input>
  </el-form-item>
  <el-form-item label="">
    <!-- .nativeDOM -->
    <el-input type="password" @keyup.enter.native="handleLogin" v-model="formData.password"></el-input>
  </el-form-item> 
  <el-form-item>
    <el-button @click="handleLogin" type="primary" class="btn"></el-button>
  </el-form-item>
</el-form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
      return {
       formData:{
         username:'',
         password:''
       }
      };
    },
    methods:{
      //
     async handleLogin(){
      var res = await axios.post('/login',{cmd:'loginAdmin',...this.formData})
    //  let { data:  { meta:{ msg,code } } } = res;
    console.log(res.data)
     //
    if(res.data.code == 1) {
     this.$message.success('');
     //  token
      // let { data:{ data:{ token } }} = res;
      let token = res.data.token;
     sessionStorage.setItem('token',token);
    //  
     this.$router.push('/goods')
    } else {
      this.$message.error(res.data.msg)
    }
  }
}
}
</script>

<style>
.login-warp{
   background-color: #324152;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
 }
.login-form{
   width: 400px;
   padding:30px;
   background-color: #fff;
   border-radius: 5px;
 }
 .btn{
   width: 100%;
 }
</style>



