import axios from 'axios';
import { Message } from 'element-ui';
import  {Loading} from 'element-ui';
const myRequire = {};
// install
myRequire.install = function(Vue) {
// vue$http
// 
  axios.defaults.baseURL = 'localhost:444';
  // axios
  // .token
  axios.interceptors.request.use(function (config) {
  // 
    if (config.url.toLocaleLowerCase() !== '/login') {
      let token = sessionStorage.getItem('token');
      config.headers.Authorization = token;
    }
    return config;
  }, function (error) {
  // 
    return Promise.reject(error);
  });
  // 
  axios.interceptors.response.use(function (response) {
  // 
  // 
  console.log(response.data)
  if (response.data.code == 3) {
    sessionStorage.setItem('token','')
    window.location.href = "/"
  }
  if (response.data.code == 1) {
  }else{
    Message.error(response.data.msg);
  }
    return response;
  }, function (error) {
  // 
    return Promise.reject(error);
  });
  // axios
  Vue.prototype.$http = axios;
  //vue
  Vue.prototype.startLoading = function(){
    const loading = this.$loading({
      lock: true,
      text: '',
      spinner: 'el-icon-loading',
      background: 'rgba(0, 0, 0, 0.7)'
    });
    return loading;
  }
  Vue.prototype.closeLoading = function(loading){
   setTimeout(()=>{
     loading.close()
   },200)
  }
};
// 
export default myRequire;



