<template>
    <er-card class="card">
        <!--  -->
        <my-breadcrumb level1="" level2=""></my-breadcrumb>
     <!--  -->
     <el-row :span="24" class="row">
         <el-button type="success" @click="addList"></el-button>
     </el-row>
     <template>
        <el-table
        border
        stripe
        :data="list"
        style="width: 100%">
        <el-table-tree-column
        prop="cat_name"
        label=""
        treeKey="cat_id"
        levelKey="cat_level"
        parentKey="cat_pid"
            width="180">
        </el-table-tree-column>
        <el-table-column
            prop="cat_level"
            label=""
            width="180">
            <template slot-scope="scope">
            <span v-if="scope.row.cat_level === 0"></span>
            <span v-if="scope.row.cat_level === 1"></span>
            <span v-if="scope.row.cat_level === 2"></span>
            </template>
        </el-table-column>
        <el-table-column
            label=""
            width="100">
            <template slot-scope="scope">
                {{scope.row.cat_deleted?"":""}}
            </template>
        </el-table-column>

        <el-table-column
        label=""
        >
        <template slot-scope="scope">
            <el-button @click = "handleEdit(scope.row)" type="primary" icon="el-icon-edit" circle></el-button>
            <el-button @click = "handleDelete(scope.row.cat_id)" type="danger" icon="el-icon-delete" circle></el-button>
        </template>
        </el-table-column>
        </el-table>
      </template>
      <!--  -->
      <el-dialog
      title=""
      :visible.sync="editVisible"
      :before-close="handleClose">
         <el-form
         :model="form"
         label-width="100px">
         <el-form-item label="">
           <el-input v-model="form.cat_name" auto-complete = "off">
           </el-input>
         </el-form-item>
    
         </el-form>
         <span slot="footer" class="dialog-footer">
            <el-button @click="erroeHandleList"> </el-button>
            <el-button type="primary" @click="editList"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="addVisible"
        :before-close="handleClose">
        <el-form
        :model="form"
        label-width="100px">
            <el-form-item label="" >
              <el-input v-model="form.cat_name" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
                <el-cascader
                    clearable
                    placeholder=""
                    :options="options"
                    expand-trigger="hover"
                    v-model="catId"
                    change-on-select
                    :props="defaultProps"
                    >
                </el-cascader>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="falseHandle"> </el-button>
            <el-button type="primary" @click="succList"> </el-button>
        </span>
</el-dialog>
    </er-card>
</template>
<script>
// treegrid 
import ElementTreegrid from 'element-tree-grid' ;
export default {
    components:{
     'el-table-tree-column':ElementTreegrid
    },
    data(){
        return {
    form:{
      cat_pid:-1,
      cat_name:'',
      cat_level:0
    },
    // id
    currentCatId:-1,
    // 
    options: [

    ],
    defaultProps:{
        value:'cat_id',
        label:'cat_name', 
        children:'children',
    },
    catId:[],
    // 
    currentCat:{},
    editVisible:false,
    // table
    list:[],
    addVisible:false     
        }
    },
    methods:{
        // table
        async loadList(){
         let res = await this.$http.get(`/categories?type=3`)
         let { meta : {msg, status}} = res.data;
         if(status == 200){
             this.list = res.data.data;
         }
        },
        async addList(){
            this.addVisible = true;
            let res = await this.$http.get('/categories?type=2');
            this.options = res.data.data;
        },
        // 
        async handleEdit(user){
           // cat_name
           this.form.cat_name = user.cat_name;
           this.currentCatId = user.cat_id;
           this.editVisible = true;
           //  
           this.currentCat = user;
        },
        // 
       async editList(user){
          let res = await this.$http.put(`categories/${this.currentCatId}`,{
               cat_name:this.form.cat_name
           });
        //    
        let { data: {meta :{ msg, status } } } = res;
        // 
        if( status = 200 ){
            this.editVisible = false;
           this.$message.success(msg);
           // 
           this.currentCat.cat_name = res.data.data.cat_name;
        }else{
            this.$message.error(msg)
        }
        },
        // 
        async handleDelete(catId){
            let catId1 = Number(catId)
             //
             this.$confirm('','',{
                 confirmButtonText:"",
                 cancelButtonText:"",
                 type:'waring'
             }).then(async()=>{
                 //
                 let res = await this.$http.delete(`categories/${catId1}`);
                //  
                let {data:{meta:{msg,status}}} = res
                if(status == 200){
                     this.$message.success(msg);
                     this.loadList();
                }else{
                    this.$message.error(msg)
                }
             }).catch(()=>{
                 this.$message({
                     type:'info',
                     message:""
                 });
             })
        },
        // 
        // cat_pid id 0 
        // cat_name
        // cat_level 0 1 2
        async succList(){
            // 
            this.form.cat_level = this.catId.length;
            // id catid0   0
              if( this.catId.length === 0 ){
                  this.form.cat_pid = 0;
              } else if (this.catId.length === 1 ){
                  this.form.cat_pid = this.catId[0];
              } else if (this.catId.length === 2 ){
                  this.form.cat_pid = this.catId[1];
              }
            let res = await this.$http.post('/categories',this.form);
            const {data: { meta: {msg, status} } } = res;
            if(status == 201){
                // 
                this.form.cat_name = '';
                // 
                this.defaultProps.value = -1;
                this.addVisible = false;
                this.$message.success(msg)
                this.loadList();
            }else{
                this.$message.error(msg)
            }
        },
        // 
        erroeHandleList(){
            this.editVisible =false;
        },
        // 
        falseHandle(){
            this.addVisible = false;
            this.form.cat_name = '';
            // 
            this.defaultProps.value = -1;
        },
    },
    created(){
      this.loadList();
    }
}
</script>
<style>
.row{
    margin-top: 10px;
    margin-bottom: 10px;
}
</style>




